@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0;
    box-sizing: border-box;
    font-family: 'Lato', sans-serif;
}

html {
    box-shadow: inset 0px 0px 50px 5px  rgba(252, 174, 252, 0.6);
    min-height: 100vh;
    overflow: auto;
}

.form-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0.2em 1em;
}

.search-form {
    display: inline-block;  
}

.logo {
    display: inline-block;
    margin: 0.5em 0;
    padding: 0.2em;
    font-style: italic;
    font-weight: bolder;
    color: rgb(7, 1, 1);
}

.initialMessage {
    font-size: 1.2rem;
    max-width: 60%;
    margin: 0 auto;
    padding: 1.5em 0;
}

.searchInput {
    margin: 0.5em 0;
    font-size: 0.6rem;
}

.searchBtn {
   padding: 0;
   font-size: 0.6rem;
   background-color: black;
   color:  rgb(255, 139, 245);
   border: 2px solid black;
}

.paginationContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 91.5%;
    margin: 0 auto;
}

.pageBtn {
    padding: 0;
    font-size: 0.6rem;
    background-color: black;
    color:  rgb(255, 139, 245);
    border: 2px solid black;
}

.resultDisplay {
    max-width: 95%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
}

.itemCard {
    margin: 0.5em;
}

.itemImg {
    max-width: 100%;  
}

.itemName, .itemPrice{
    font-size: 0.4rem;
    margin: 0.3em 0;
}

.itemMsrp {
    font-size: 0.4rem;
    margin: 0.3em 0;
    text-decoration: line-through;
    font-style: italic;

}

input {
    border: 1px solid rgb(167, 14, 154);
}

button {
    padding: 0;
    font-size: 0.6rem;
    background-color: black;
    color:  rgb(255, 139, 245);
    border: 2px solid black;
}


